<template>
    <div style="padding: 30px 0;">
   <div class="d-tabs">
       <div class="d-tabs-head">
           <div class="tab-nav"> 
               <div class="tab-nav-item" :class="{'active':tabCur==0}" @click="tabClick(0)">充值记录</div>
               <div class="tab-nav-item" :class="{'active':tabCur==1}" @click="tabClick(1)">提现记录</div>
            </div>
        </div> 
        <div class="d-tabs-content">
            <div class="tab-pane" label="充值记录" v-if="tabCur==0">
                <!-- <div class="search-form">
                    <div class="form-item">
                        <span>时间：</span>
                        <el-date-picker
                            v-model="value1"
                            type="daterange"
                            range-separator="至"
                            start-placeholder="开始日期"
                            end-placeholder="结束日期">
                        </el-date-picker>
                    </div>

                    <div class="form-item">
                        <span>账变类型：</span>
                         <el-select v-model="value" placeholder="请选择">
                            <el-option
                            v-for="item in options"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                            </el-option>
                        </el-select>
                    </div>

                    <div class="form-item">
                        <button class="sm-btn clear-btn" style="margin-right: 10px;">
                            <img src="../assets/img/clear.d841abf.png" alt="">清空
                        </button> 
                        <button class="sm-btn">
                            <img src="../assets/img/search.85d7e0a.png" alt="">搜索
                        </button>
                    </div>                     
                </div> -->

                 <el-table
                    :data="yhhdList"
                    style="width: 100%;margin-top:20px;">
                    <el-table-column
                        prop="order_no"
                        label="编号"
                        align="center"
                       >
                    </el-table-column>
                    <el-table-column
                        prop="add_time"
                        label="时间"
                        align="center"
                        >
                    </el-table-column>
                    <el-table-column                        
                        label="账变类型"
                        align="center">
                        <template v-slot="scope">
                            <span  :class="['text-orange','text-green','text-red'][scope.row.status]">
                            {{['待审核','已审核','撤销'][scope.row.status]}}
                            </span>
                        </template>
                    </el-table-column>
                     <el-table-column
                        prop="amount"
                        label="变动资金"
                        align="center">
                    </el-table-column>                      
                </el-table>
            </div>
            <div class="tab-pane" label="充值记录" v-if="tabCur==1">
                <el-table
                    :data="yhhdList"
                    style="width: 100%;margin-top:20px;">
                    <el-table-column
                        prop="order_no"
                        label="编号"
                        align="center"
                       >
                    </el-table-column>
                    <el-table-column
                        prop="add_time"
                        label="时间"
                        align="center"
                        >
                    </el-table-column>
                    <el-table-column                        
                        label="账变类型"
                        align="center">
                        <template v-slot="scope">
                            <span v-if="scope.row.status != 2" :class="['text-orange','text-green','text-red'][scope.row.status]">{{['已申请提现','提现完成','取消打款'][scope.row.status]}}</span>
                            <span v-else :class="['text-orange','text-green','text-red'][scope.row.status]">提现失败：{{scope.row.reason}}</span>
                        </template>
                    </el-table-column>
                     <el-table-column
                        prop="amount"
                        label="变动资金"
                        align="center">
                    </el-table-column>                      
                </el-table>
            </div>
            
        </div>

        <div style="margin: 20px auto;width: 1200px;text-align:center;">
        <el-pagination          
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="curPage"
            :page-sizes="[10, 20, 30, 40]"
            :page-size="limit"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total">
        </el-pagination>
    </div>

    </div>

    
    </div>
</template>

<script>
import { czList,txList } from "../api/public";
export default {
  data() {
    return {
      tabCur: 0,
      yhhdList: [],
      curPage: 1,
      total: 1,
      limit: 10,
    };
  },
  
   watch: {
    $route: "fetchData"
  },
  mounted() {
      console.log(this.$route.query)
      this.tabCur = this.$route.query.id || 0
      this.discountList()
  },
  methods: {
       fetchData(newVal, oldVa) {
          console.log(oldVa)//oldVa 上一次url
          console.log(newVal);//newVal 这一次的url
            this.tabCur = newVal.query.id
            this.discountList()
        },
    tabClick(i) {
        this.tabCur = i
        this.discountList()
    },
    discountList() {
        let _this = this				
        if (this.tabCur == 0) {
            czList({"page": _this.curPage, "limit": _this.limit, "searchParams": {
                "user_id": JSON.parse(localStorage.getItem("user_info")).id
            }}).then(res => {
                console.log(res)
                if (res.code == 1) {
                    this.yhhdList = res.data
                    this.total = res.count                   
                }
            })
        } else {
            txList({"page": _this.curPage, "limit": _this.limit, "searchParams": {
                "user_id": JSON.parse(localStorage.getItem("user_info")).id
            }}).then(res => {
                console.log(res)
                if (res.code == 1) {
                    this.yhhdList = res.data
                    this.total = res.count                   
                }
            })
        }    
    },
    handleSizeChange(val) {
        console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
        this.discountList()
        console.log(`当前页: ${val}`);
    }
  }
}
</script>

<style>

.d-tabs {
    width: 1200px;
    min-height: 630px;
    margin: 0 auto;
    background: #fff;
    box-shadow: 0 2px 4px 0 rgba(0,0,0,.12),0 0 6px 0 rgba(0,0,0,.04);
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    position: relative
}

.d-tabs .d-tabs-head {
    overflow: hidden;
    height: 40px
}

.d-tabs .inline-item {
    background: #282d4f;
    width: 100%;
    font-size: 0
}

.d-tabs .inline-item .tab-nav-item {
    letter-spacing: 1px;
    display: inline-block;
    padding: 0 10px;
    color: #fff;
    font-size: 14px;
    height: 26px;
    line-height: 26px;
    transition: all .3s;
    margin: 7px 10px;
    position: relative;
    border-radius: 4px
}

.d-tabs .inline-item .tab-nav-item:hover {
    color: #ec002c;
    text-decoration: underline;
    cursor: pointer
}

.d-tabs .inline-item .active {
    background-image: linear-gradient(-180deg,#d4d4d4,#fff);
    box-shadow: 0 2px 4px 0 rgba(0,0,0,.5);
    background: #fff;
    color: #282d4f
}

.d-tabs .inline-item .active:hover {
    color: #282d4f;
    text-decoration: none
}

.d-tabs .tab-nav {
    background: #282d4f;
    width: 100%;
    font-size: 0
}

.d-tabs .tab-nav .tab-nav-item {
    letter-spacing: 1px;
    display: inline-block;
    padding: 0 32px;
    color: #fff;
    font-size: 14px;
    height: 40px;
    line-height: 40px;
    font-weight: 700;
    transition: all .3s;
    position: relative
}

.d-tabs .tab-nav .tab-nav-item:hover {
    background-color: #7e5acf;
    cursor: pointer
}

.d-tabs .tab-nav .active {
    background: #fff;
    color: #282d4f
}

.d-tabs .tab-nav .active:hover {
    background-color: #fff;
    color: #282d4f;
    cursor: inherit
}

.d-tabs .both-side {
    background: #282d4f;
    width: 100%;
    font-size: 0
}

.d-tabs .both-side .tab-nav-item {
    letter-spacing: 1px;
    display: inline-block;
    position: relative;
    margin: 0 32px;
    color: #ec002c;
    font-size: 14px;
    height: 40px;
    line-height: 40px;
    font-weight: 700
}

.d-tabs .both-side .tab-nav-item:before {
    content: "";
    height: 4px;
    width: 4px;
    background: #ec002c;
    position: absolute;
    left: -10px;
    top: 17px
}

.d-tabs .both-side .tab-nav-item:after {
    content: "";
    height: 4px;
    width: 4px;
    background: #ec002c;
    position: absolute;
    right: -10px;
    top: 17px
}

.d-tabs .show-tab {
    position: absolute;
    width: 100%;
    top: 0;
    z-index: 1
}

.d-tabs .show-tab .tav-rate {
    visibility: visible;
    opacity: 1
}
</style>